@import url(https://fonts.googleapis.com/css2?family=Kiwi+Maru:wght@400;500&family=Roboto:wght@300;400;500;700&display=swap);
.cart-button{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;cursor:pointer}.cart-button__img{max-width:30px}.cart-button__span{margin-left:5px}
.header{width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;box-shadow:1px 1px 3px rgba(0,0,0,.25);border-left:none;border-right:none;height:5em;margin-bottom:10px}.header__container{width:85%;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.header__title{font-family:"Kiwi Maru",serif;font-weight:bold;font-size:25px;color:#000}.header a{text-decoration:none}
